<template>

  <section>

    <!---->
    <el-breadcrumb separator="/" class="bread-nav">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true">
        <el-col :span="4">
          <el-form-item>
            <el-input placeholder="" v-model="add_custom.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-input placeholder="" v-model="add_custom.phone"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-button type="primary" @click="addCustom"></el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="custom_table_data" style="width: 100%" v-loading="config.custom_table_loading">
      <el-table-column type="index" width="80"></el-table-column>
      <el-table-column v-if="config.id_show">
        <template scope="scope">
          <el-tag>{{ scope.row.id }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="success">{{ scope.row.phone }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" @click="handleEdit( scope.row.name, scope.row.phone,scope.row.id)"></el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="config.dialogVisible" size="tiny" class="model">
      <el-input v-model="edit_custom.name">
        <template slot="prepend"></template>
      </el-input>
      <br><br>
      <el-input v-model="edit_custom.phone">
        <template slot="prepend"></template>
      </el-input>
      <br><br>
      <span slot="footer" class="dialog-footer">
        <el-button @click="config.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-pagination layout="prev, pager, next" :total="page.total" :page-size="15" @current-change="currentChange"
                   style="float: right;margin-top: 20px">
    </el-pagination>

  </section>

</template>

<script>

  import axios from 'axios';

  let qs = require("qs");

  export default {
    data() {
      return {
        config: {
          id_show: false,
          custom_table_loading: false,
          dialogVisible: false
        },
        // 
        custom_table_data: [],
        // 
        add_custom: {
          name: '',
          phone: ''
        },
        // 
        edit_custom: {
          name: '',
          phone: '',
          id: ''
        },
        // 
        page: {
          current_page: 1,
          total: 0
        }
      }
    },
    methods: {
      // 
      getCustomData() {
        this.config.custom_table_loading = true;
        axios.post('customer_queryAllCustomer.ajax', qs.stringify({
          startPage: this.page.current_page
        }))
          .then((res) => {
            this.config.custom_table_loading = false;
            this.custom_table_data = res.data.list;
            this.page.total = res.data.page.totalCount;

          });
      },
      // 
      addCustom() {
        axios.post('customer_addCustomer.ajax', qs.stringify({
            name: this.add_custom.name,
            phone: this.add_custom.phone
          })
        )
          .then((res) => {
            if (res.data.data === true) {
              this.$message("");
              this.getCustomData();
            } else {
              this.$message({
                duration: 1000,
                message: res.data.msg,
                type: 'warning'
              });
            }
          });
      },
      // 
      handleEdit(name, phone, id) {
        this.config.dialogVisible = true;
        this.edit_custom.name = name;
        this.edit_custom.phone = phone;
        this.edit_custom.id = id;
      },
      // 
      submitEdit() {
        axios.post('customer_editCustomer.ajax', qs.stringify({
          name: this.edit_custom.name,
          phone: this.edit_custom.phone,
          id: this.edit_custom.id
        })).then((res) => {
          if (res.data.data === true) {
            this.config.dialogVisible = false;
            this.$message({
              duration: 1000,
              message: '',
              type: 'success'
            });
          } else {
            this.$message({
              duration: 1000,
              message: res.data.msg,
              type: 'warning'
            });
          }
        })
      },
      // 
      handleDelete(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {// 
          axios.post('customer_deleteCustomerByIds.ajax', qs.stringify({
              ids: id
            })
          )
            .then((res) => {
              this.$message("");
              this.getCustomData();
            });
        }).catch(() => {// 
          this.$message({
            type: 'info',
            duration: 1000,
            message: ''
          });
        });
      },
      // 
      currentChange(page) {
        this.page.current_page = page;
        this.getUserData();
      }
    },
    mounted: function () {
      this.getCustomData();
    }
  }

</script>

<style scoped>
  * {
    margin: 0;
  }

  .bread-nav {
    float: right;
    margin-bottom: 10px;
  }

  .toolbar {
    background: #f2f2f2;
    margin: 5px 0;
  }

  .toolbar .el-form-item {
    margin: 20px 0 5px 20px;
  }

</style>



